import React, { Component } from 'react';
import ColumnAliasItem from "./ColumnAliasItem";
import Popup from "reactjs-popup";

export default class SettingPopUp extends Component {
    constructor(props){
        super(props);

        this.state = {
            visible  : false
        };
        //console.log('setting pop -> ', this.props);
        this.saveChanges = this.saveChanges.bind(this);
        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    openModal() {
        this.setState({
            visible : true
        });
    }

    closeModal() {
        this.setState({
            visible : false
        });
    }

    saveChanges() {
        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/set_forms_option/` ;

        fetch(BASE_URL, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                columns: this.props.columnalias,
                formName: this.props.formName,
                pluginName: this.props.pluginName,
            })
        }).then(response => response.json()).then(data => {

        });
        //console.log('props',this.props);
        this.closeModal();
    }
    render() {
        //console.log('setting pop -> ', this.props);
        return[
                    <button className="button" onClick={this.openModal} style={{display: 'inline-flex' }}>
                        <i className="dashicons dashicons-visibility"> </i>
                    </button>,
                        <Popup
                            open={this.state.visible}
                            closeOnDocumentClick
                            closeOnEscape
                            onClose={this.closeModal}
                            className={ 'fv-setting-popup'}
                            modal
                        >
                            <ul>
                                <div className="modal">
                                    <a className="close" onClick={this.closeModal}>
                                        X
                                    </a>
                                    <ColumnAliasItem updateOrder={this.props.updateOrder} col={this.props.columnalias} updateColumnAlias={this.props.updateColumnAlias} onDragEnd={this.props.onDragEnd}/>
                                    <div className={'popup-footer'}>
                                        <button className={'button fv-btn-save-setting'} onClick={this.saveChanges}>Save Changes</button>
                                    </div>
                                </div>
                            </ul>
                        </Popup>
        ]
    }
}