import React, { Component } from 'react';
import Configuration from "./Configuration";
import TopBar from "./topBar";
import Sidebar from "./sidebar";
const { TabPanel } =  wp.components;

let RoleManager = '';
let ExcludeForms = '';
if(fvGlobalVar.fv_plan === 'PAID'){
    RoleManager = require('./pro/roleManager').default;
    ExcludeForms = require('./pro/excludeForms').default;
}

export default class DashboardSettings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: 'false'
        }
    }
    set_load_status(status){
        this.setState({loading:status});
    }

    render() {
        let tabPanelTabs = [
            {
                name: 'fv-configuration',
                title: 'Configuration',
                className: 'fv-tab-element fv-config-wrapper'
            }
        ];

        fvGlobalVar.fv_plan === 'PAID' && (
            tabPanelTabs.push({
                name: 'fv-role-manager',
                title: 'Role Manager',
                className: 'fv-tab-element fv-role-manager-wrapper'
            },
            {
                name: 'fv-exclude-forms',
                title: 'Exclude Forms',
                className: 'fv-tab-element fv-exclude-form-wrapper'
            }
            )
        );

        return[
            <div className="fv-wrapper">
                <TopBar/>
                <div className={'fv-db-settings'}>
                    <div className={`fv-settings-control-wrapper ${ this.state.loading === 'true' ? 'loading' : 'loaded'}`}>
                        <TabPanel className={`fv-setting-tabs ${ this.state.loading === 'true' ? 'loading' : 'loaded'}`}
                                  activeClass="fv-active"
                            //onSelect={ onSelect }
                                  tabs={ tabPanelTabs }>
                            {
                                ( tab ) => [
                                    tab.name === 'fv-configuration' && (
                                        <Configuration set_status={this.set_load_status.bind(this)}/>
                                    ),
                                    tab.name === 'fv-role-manager' && (
                                        <RoleManager set_status={this.set_load_status.bind(this)}/>
                                    ),
                                    tab.name === 'fv-exclude-forms' && (
                                        <ExcludeForms set_status={this.set_load_status.bind(this)}/>
                                    )
                                ]
                            }
                        </TabPanel>

                    </div>
                    <div className={'fv-sidebar'}>
                        <Sidebar/>
                    </div>
                </div>
            </div>
        ]
    }
}