import React, { Component } from 'react';

export default class TopBar extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        var url = window.location.search;
        url = url.replace("?", '');
        var activeUrl = url.substr(5,url.length);
        return[
            <div className="fv-headBar">
                <div className="fv-logo">
                    Form Vibes
                    <span className="fv-version">{fvGlobalVar.fv_version}</span>
                </div>
                <div className="fv-menu">
                    <a className={activeUrl === 'fv-leads' ? 'menu active' : 'menu'} id="leads" href="?page=fv-leads" >Data</a>
                    <a className={activeUrl === 'fv-analytics' ? 'menu active' : 'menu'}  id="analytics" href="?page=fv-analytics">Analytics</a>
                    <a className={activeUrl === 'fv-db-settings' ? 'menu active' : 'menu'}  id="settings" href="?page=fv-db-settings">Settings</a>
                </div>
            </div>
        ]
    }
}