import React, { Component } from 'react';
import jquery from 'jquery';
import Select2 from 'react-select2-wrapper';
const { Button,RadioControl } =  wp.components;

export default class ExcludeForms extends Component {
    constructor(props) {
        super(props);

        this.state = {
            forms: {},
            pro: true,
            selectedForms:{}
        };

        this.get_forms();

    }

    get_forms(){
        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_all_forms/`;
        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
        }).then(response => response.json()).then(data => {
            console.log('data',data);
            //this.setState({forms : data});

            var allForms = {};

            Object.keys(data['allForms']).map(key => {
                    const forms = [];
                    {
                        Object.keys(data['allForms'][key]).map(formKey => {
                                forms.push({
                                    id: data['allForms'][key][formKey].id,
                                    text: data['allForms'][key][formKey].name
                                })
                            }
                        )
                    }
                    allForms[key] = forms;
                }
            );
            console.log('all forms ', allForms);
            this.setState({forms : allForms});
            if(data['selectedForms'] !== false && data['selectedForms'] !== ''){
                this.setState({selectedForms : data['selectedForms']});
            }
        });
    }


    updateSelectedForm(event){
        let excludeForms = this.state.selectedForms;

        if(excludeForms === ''){
            console.log('in if');
            //excludeForms = [];
        }

        let selectedForms = event.target.selectedOptions;

        excludeForms[event.target.name] = {};

        Object.keys(selectedForms).map(key =>
            excludeForms[event.target.name][selectedForms[key].value] = selectedForms[key].text
        );
        this.setState({selectedForms : excludeForms},() => {console.log('selected form after-> ', this.state.selectedForms);});
    }

    saveSettings(event){
        this.props.set_status('' +
            '');
        jQuery.ajax({
            type: 'POST',
            url: fvGlobalVar.ajax_url,
            data: {
                action : 'fv_save_exclude_forms',
                myForms: this.state.selectedForms,
            },
        })
            .done(function(data) {
                this.props.set_status('false');
            }.bind(this))
            .fail(function(jqXhr) {
                console.log('failed to save settings');
            });
    }
    render() {
        return [
            <form method="POST">
                <div className="configuration-wrapper">

                    {
                        Object.keys(this.state.forms).map(key => [
                        <div className={'fv-control-wrapper'}>
                            <span className='labels'>
                                Plugin {key}
                            </span>
                            {
                                <Select2
                                    id={key}
                                    name={key}
                                    className='my-select-class'
                                    multiple
                                    onChange={this.updateSelectedForm.bind(this)}
                                    data={ this.state.forms[key]}
                                    defaultValue={ this.state.selectedForms[key] !== undefined ? Object.keys(this.state.selectedForms[key]) : ''}
                                />
                            }
                        </div>,
                        ])
                    }
                    <div className={'fv-control-wrapper'}>
                        <Button name="btnSaveForms" isPrimary onClick={this.saveSettings.bind(this)}>
                            Save Data
                        </Button>
                    </div>
                </div>
            </form>
        ]
    }
}