import React, { Component } from 'react';

export default class EntryCount extends Component {
    constructor(props) {
        super(props);
        this.state = {
            forms_enteries: {},
        }
    }

    componentWillReceiveProps(nextProps, nextContext) {
        this.get_entry_count_data();
    }

    get_entry_count_data(){
        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_entry_count/`;
        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                duration: this.props.duration,
            })
        }).then(response => response.json()).then(data => {
            this.setState({forms_enteries : data});
        });
    }
    render() {
        return(
            <div className={'fv-entry-count'}>
                <div>
                    <h3 className={'fv-entry-title'}>Total Entries By Forms ({this.state.forms_enteries.fromDate +' To '+ this.state.forms_enteries.toDate })</h3>
                </div>
                {
                    Object.keys(this.state.forms_enteries).map(key =>

                        key !== 'fromDate' && key !== 'toDate' && (
                            <div className={'fv-db-entry-wrapper'}>
                            <span className={'fv-db-form-name'}>
                                {this.state.forms_enteries[key].formName }
                                &nbsp;({this.state.forms_enteries[key].plugin})
                            </span>
                                <span className={'fv-db-form-enteries'}>
                                {this.state.forms_enteries[key].count}
                            </span>
                            </div>
                        )
                    )
                }
            </div>
        )
    }
}