import React from 'react';
import ReactDOM from 'react-dom';
import Header from '../component/header.jsx'
import Analytics from '../component/analytics.jsx'
import Dashboard from "../component/Dashboard";
import DashboardSettings from "../component/DashboardSettings";

document.addEventListener('DOMContentLoaded', function() {
  if(document.getElementById('fv-leads') !== null)
  {
    ReactDOM.render(
        <Header/>,document.getElementById('fv-leads'),
    );
  }
  if(document.getElementById('fv-analytics') !== null)
  {
    ReactDOM.render(
        <Analytics/>,document.getElementById('fv-analytics'),
    );
  }
  if(document.getElementById('fv-dashboard-widgets-0') !== null)
  {
    let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_dbpanel_settings/`;
    fetch(BASE_URL,{
      method:'POST',
      headers: {
        'X-WP-Nonce' : fvGlobalVar.nonce
      },
    }).then(response => response.json()).then(resData => {
      let elements = document.getElementsByName('dashboard-widget');
      for(let i=0; i<elements.length; i++){
        ReactDOM.render(
            <Dashboard widgetsData={resData['panelData'][i]} dashboard_number={i}/>,document.getElementById(elements[i].id),
        );
      }
    });
  }
  if(document.getElementById('fv-db-settings') !== null)
  {
    ReactDOM.render(
        //<Dashboard_Settings/>,document.getElementById('fv-db-settings'),
        <DashboardSettings/>,document.getElementById('fv-db-settings'),
    );
  }

});
