        if(this.state.expanded) {
            const expanded = this.state.expanded;
            Object.keys(expanded).map(value => {
                expanded[value] = false;
            });
            this.setState({expanded});
        }

        if(fromDate !== undefined){
            fromDate = new Date(fromDate);
            var month = fromDate.getMonth() + 1;
            var day = fromDate.getDate();
            var year = fromDate.getFullYear();

            fromDate = year +'-'+month+'-'+day;
        }
        var toDate = this.state.to;
        if(toDate !== undefined){
            toDate = new Date(toDate);
            var month = toDate.getMonth() + 1;
            var day = toDate.getDate();
            var year = toDate.getFullYear();

            toDate = year +'-'+month+'-'+day;
        }
        param['fromDate'] = fromDate;
        param['toDate'] = toDate;
        param['plugin'] = this.state.selectedPlugin;
        param['formid'] = this.state.selectedForm;

        //BASE_URL = BASE_URL + '&fromDate='+ fromDate;
        //BASE_URL = BASE_URL + '&toDate='+ toDate;
        //BASE_URL = BASE_URL + '&plugin='+ this.state.selectedPlugin;
        //BASE_URL = BASE_URL + '&formid='+ this.state.selectedForm;

        if(fvGlobalVar.fv_plan === 'PAID'){
            param['filter'] = this.state.selectedFilter;
            param['filterValue'] = this.state.filterValue;
            param['filterOperator'] = this.state.filterOperator;

            //BASE_URL = BASE_URL + '&filter='+ this.state.selectedFilter;
            //BASE_URL = BASE_URL + '&filterValue='+ this.state.filterValue;
            //BASE_URL = BASE_URL + '&filterOperator='+ this.state.filterOperator;
        }

        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'dataType' : 'json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                data: param,
            }),
        }).then(response => response.json()).then(data => {
            /*Object.keys(data['columns']).map(i => {
                if(data['columns'][i] === 'datastamp' || data['columns'][i] === 'captured'){
                    data['columns'][i] = 'Submission Date';
                }
            })*/
            if(data == null){
                this.setState({loading : false});
                var plugins = {no_plugin: "No Plugin"};
                var forms = {'no_form' : { 'id' : 'no_form', 'name' : 'No Form'}};
                this.setState({formName : forms});
                this.setState({ formsPlugin: plugins });
                return;
            }
            let leads_obj = data['leads'];
            var leads = Object.keys(leads_obj).map(function(key) {
                return leads_obj[key];
            });
            this.setState({leadsLength:leads.length});
            if(this.state.selectedFilter === ''){
                this.setState({ selectedFilter: data['columns'][0]});
            }
            this.setState({firstForm:data['columns'][0]});
            this.setState({ pages: data.lead_count });
            this.setState({ formsPlugin: data.forms_plugin });
            this.setState({ csvData: leads });
            this.cols = [];
            this.cols = data['columns'];
            this.setState({origCols: this.cols});
            if(this.state.freshLoad === true){
                let form_plugin = data['forms_plugin'];
                this.setState({selectedPlugin : Object.keys(form_plugin)[0]});

                this.fillForms(Object.keys(form_plugin)[0],leads);
                //this.setState({freshLoad: false});

            }
            this.render_form_option(leads);
        });
