import React, { Component } from 'react';
import {Bar, Pie} from 'react-chartjs-2';
import DayPicker, { DateUtils, LocaleUtils } from 'react-day-picker';
const { DatePicker,Button,Dropdown,TabPanel,RadioControl } =  wp.components;
const { __ } = wp.i18n;
const { Fragment } = wp.element;
import TopBar from "./topBar"

let Unauthorised = '';
if(fvGlobalVar.fv_plan === 'PAID'){
    Unauthorised = require('./pro/unauthorised').default;
}

export default class Analytics extends Component {
    constructor(props) {
        super(props);
        this.state={
            from: undefined,
            to: undefined,
            formsPlugin: [],
            formName:[],
            selectedPlugin: '',
            selectedForm:'',
            data:{},
            freshLoad : true,
            filterType : 'day',
            queryType : 'Last_30_Days',
            wrapperRef:'',
            wrapperRefTo:'',
            options:{
                'legend':{
                    display:false,
                },
                'scales':{
                    yAxes: [{
                        ticks: {
                            min: 0,
                        }
                    }]
                },
                title: {
                    display: true,
                    text: 'Weekly Report'
                }
                }
            };

        this.customToolTip = this.customToolTip.bind(this);
        this.getData();
    }

    setToday(){
        var today = new Date();
        var dd = today.getDate();

        var mm = today.getMonth()+1;
        var yyyy = today.getFullYear();
        if(dd<10)
        {
            dd='0'+dd;
        }

        if(mm<10)
        {
            mm='0'+mm;
        }
        today = yyyy+'-'+mm+'-'+dd;
        return today;
    }
    setfrom(){
        var today = new Date();
        today.setDate(today.getDate());

        var dd = today.getDate();

        var mm = today.getMonth();
        var yyyy = today.getFullYear();
        if(dd<10)
        {
            dd='0'+dd;
        }

        if(mm<10)
        {
            mm='0'+mm;
        }
        today = yyyy+'-'+mm+'-'+dd;
        //this.setState({date : today});
        //console.log('state',this.state)
        return today;
    }
    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside.bind(this));
     }
    
    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside.bind(this));
    }

    setWrapperRef(node) {
        this.state.wrapperRef = node;
    }
    setwrapperRefTo(node) {
        this.state.wrapperRefTo = node;
    }

    handleClickOutside(event) {
        if (this.state.wrapperRef && !this.state.wrapperRef.contains(event.target)) {
            this.setState({isVisible : false});
        }
        if (this.state.wrapperRefTo && !this.state.wrapperRefTo.contains(event.target)) {
            this.setState({isVisibleto : false});
        }
    }

    
    fillForms(value) {
        const BASE_URL = fvGlobalVar.rest_url + `wts-fv/get_forms/`;

        fetch(BASE_URL, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'dataType' : 'json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                post_type: value,
            }),
        }).then(response => response.json()).then(data => {
            this.setState({formName : data}, ()=> { this.setState({selectedForm : Object.keys(data)[0]})});
        });

        //let selects = document.querySelectorAll('#ddlForm');

        //var e = document.createEvent('HTMLEvents');
        //e.initEvent('onChange', false, true);
        //selects[0].dispatchEvent(e);

    }

    getData(){
        let param = {};
        var fromDate = this.state.from;
        if(fromDate !== undefined){
            fromDate = new Date(fromDate);
            var month = fromDate.getMonth() + 1;
            var day = fromDate.getDate();
            var year = fromDate.getFullYear();

            fromDate = year +'-'+month+'-'+day;
        }
        var toDate = this.state.to;
        if(toDate !== undefined){
            toDate = new Date(toDate);
            var to_month = toDate.getMonth() + 1;
            var to_day = toDate.getDate();
            var to_year = toDate.getFullYear();
            toDate = to_year +'-'+ to_month+'-'+to_day;
        }

        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_analytic_data/`;

        param['queryType'] = this.state.queryType;
        param['fromDate'] = fromDate;
        param['toDate'] = toDate;
        param['pluginName'] = this.state.selectedPlugin;
        param['formName'] = this.state.selectedForm;
        param['filterType'] = this.state.filterType;
        /*BASE_URL = BASE_URL + '?queryType='+ this.state.queryType;
        BASE_URL = BASE_URL + '&fromDate='+ fromDate;
        BASE_URL = BASE_URL + '&toDate='+ toDate;
        BASE_URL = BASE_URL + '&pluginName='+ this.state.selectedPlugin;
        BASE_URL = BASE_URL + '&formName='+ this.state.selectedForm;
        BASE_URL = BASE_URL + '&filterType='+ this.state.filterType;*/

        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'dataType' : 'json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                data: param,
            }),
        }).then(response => response.json()).then(resData => {
            if(resData == null){
                var plugins = {no_plugin: "No Plugin"};
                var forms = {'no_form' : { 'id' : 'no_form', 'name' : 'No Form'}};
                this.setState({formName : forms});
                this.setState({ formsPlugin: plugins });
                return;
            }
            this.setState({ formsPlugin: resData.forms_plugin });


            if(this.state.freshLoad === true){
                this.setState({freshLoad: false});

                let form_plugin = resData['forms_plugin'];

                this.setState({selectedPlugin : Object.keys(form_plugin)[0]});
                this.fillForms(Object.keys(form_plugin)[0]);
            }


            let chartData = [];
            chartData['labels'] = [];
            chartData['datasets'] = [];
            chartData['data'] = [];

            resData.data.map((row, key) =>
            {
                chartData['labels'].push(row.Label);
                chartData['data'].push(row.count);
            });
            this.setState({data :{
                    labels : chartData['labels'],
                    datasets: [{
                        label: 'Leads',
                        backgroundColor: "#0087be",
                        hoverBackgroundColor: "#00aadc",
                        data: chartData['data'],
                    }]
                } });

        });
    }

    handleClick(event){
        event.preventDefault();
        this.getData();
    }

    filterData(event){
        this.setState({filterType : event.target.name},()=>{this.getData()});
    }
    customToolTip(TooltipItem, object){
        return 'hello';
    }
        onTabSelect( tab ) {
        event.preventDefault();
        this.setState({ tabSelected : tab });
        if(tab === 'custom'){
            this.setState({ queryType : 'Custom' });
        }
        else{
            this.setState({ queryType : 'Last_30_Days' });
        }
    }

    handleDayClick(day) {
        const range = DateUtils.addDayToRange(day, this.state);
        this.setState({from : range.from});
        this.setState({to : range.to});
    }

    handleResetClick() {
        this.setState({from : undefined});
        this.setState({to : undefined});
    }
    onChangeQuery(option) {
        this.setState({queryType : option});
        const val = option;
        if(val === 'Yesterday' || val === 'Today' || val === 'This_Week' || val === 'Last_Week' || val === 'Last_30_Days' || val === 'Custom' || val === 'Last_Month' || val === 'This_Month' ){
            this.setState({filterType : 'day'})
        }
        if(val === 'This_Quarter' || val === 'Last_Quarter' || val === 'Last_Quarter' || val === 'This_Year' || val === 'Last_Year'){
            this.setState({filterType : 'month'})
        }

    };

    render_controls(){
        const from = this.state.from;
        const to = this.state.to;
        const modifiers = { start: from, end: to };
        return  <div className="fv-analytics-wrapper">
            <div className="fv-control-wrapper">
                <form>
                    <div className="fv-forms-control-wrapper">
                        <Dropdown
                            className="fv-date-wrapper fv-form-control"
                            contentClassName="fv-date-content"
                            position="bottom center"
                            renderToggle={ ( { isOpen, onToggle } ) => (
                                <Button className="fv-date-range-button" isPrimary onClick={ onToggle } aria-expanded={ isOpen }>
                                    Date Range ({this.state.queryType.replace(/_/g,' ')}
                                    {
                                        this.state.queryType === 'Custom' && [
                                            typeof this.state.from !== 'undefined' && this.state.from !== null && (
                                                ' '+this.state.from.toLocaleDateString()
                                            ),
                                            typeof this.state.to !== 'undefined' && this.state.to !== null &&(
                                                ' To '+this.state.to.toLocaleDateString()
                                            )
                                        ]
                                    }
                                    )
                                </Button>
                            ) }
                            renderContent={ () => (
                                <div>
                                    <span className="fv-date-heading">Select A Date Range</span>
                                    <TabPanel
                                        tabs={ [
                                            {
                                                name: 'period',
                                                title: __( 'Presets', 'woocommerce-admin' ),
                                                className: 'fv-date-period',
                                            },
                                            {
                                                name: 'custom',
                                                title: __( 'Custom', 'woocommerce-admin' ),
                                                className: 'fv-date-custom',
                                            },
                                        ] }
                                        className="fv-date-tabs"
                                        activeClass="is-active"
                                        initialTabName={ 'custom' === this.state.tabSelected ? 'custom' : 'period' }
                                        onSelect={ this.onTabSelect.bind(this) }
                                    >
                                        { selected => (
                                            <Fragment>
                                                { selected.name === 'period' && [
                                                    <RadioControl
                                                        selected={ this.state.queryType }
                                                        className='fv-rb-option'
                                                        options={ [
                                                            { value: 'Today', label: 'Today' },
                                                            { value: 'Yesterday', label: 'Yesterday' },
                                                            { value: 'This_Week', label: 'This Week' },
                                                            { value: 'Last_Week', label: 'Last Week' },
                                                            { value: 'Last_30_Days', label: 'Last 30 Days' },
                                                            { value: 'This_Month', label: 'This Month' },
                                                            { value: 'Last_Month', label: 'Last Month' },
                                                            { value: 'This_Quarter', label: 'This Quarter' },
                                                            { value: 'Last_Quarter', label: 'Last Quarter' },
                                                            { value: 'This_Year', label: 'This Year' },
                                                            { value: 'Last_Year', label: 'Last Year' },
                                                        ] }
                                                        onChange={ ( option ) => { this.onChangeQuery(option) } }
                                                    />
                                                ] }
                                                { selected.name === 'custom' && [
                                                    <p className='fv-date-text'>
                                                        {!from && !to && 'Please select the first day.'}
                                                        {from && !to && 'Please select the last day.'}
                                                        {from &&
                                                        to &&
                                                        `${from.toLocaleDateString()} to
                                                                            ${to.toLocaleDateString()}`}{' '}
                                                        {from &&
                                                        to && (
                                                            <button className="link" onClick={this.handleResetClick.bind(this)}>
                                                                <i className="dashicons dashicons-image-rotate"></i>
                                                            </button>
                                                        )}
                                                    </p>,
                                                    <DayPicker
                                                        className="Selectable"
                                                        numberOfMonths='1'
                                                        selectedDays={[from, { from, to }]}
                                                        onDayClick={this.handleDayClick.bind(this)}
                                                        modifiers={modifiers}
                                                    />,
                                                ] }
                                            </Fragment>
                                        ) }
                                    </TabPanel>
                                </div>
                            ) }
                        />
                        <div>
                            <span className="fv-form-control">Select Plugin</span>
                            <select id='ddlPlugin' className="fv-form-control" onChange={ (event) => this.setState({selectedPlugin : event.target.value},this.fillForms(event.target.value))} >
                                {
                                    Object.keys(this.state.formsPlugin).map(key =>
                                        <option value={key}>{this.state.formsPlugin[key]}</option>
                                    )
                                }
                            </select>
                        </div>
                        <div>
                            <span className="fv-form-control">Select Form</span>
                            <select id='ddlForm' className="fv-form-control" value={this.state.selectedForm} onChange={ (event) => { this.setState({selectedForm : event.target.value})} } >
                                {
                                    Object.keys(this.state.formName).map(key =>
                                        <option value={this.state.formName[key].id}>{this.state.formName[key].name}</option>
                                    )
                                }
                            </select>
                        </div>
                        <div><button className="button" onClick={this.handleClick.bind(this)}>Get Data</button></div>
                    </div>
                </form>

                <div className="fv-analytics-time">
                    <a className={`${this.state.filterType == 'day' ? 'active' : 'inactive'}`} onClick={this.filterData.bind(this)} name="day">Day</a>
                    <a className={`${this.state.filterType == 'week' ? 'active' : 'inactive'}`} onClick={this.filterData.bind(this)} name="week">Week</a>
                    <a className={`${this.state.filterType == 'month' ? 'active' : 'inactive'}`} onClick={this.filterData.bind(this)} name="month">Month</a>
                </div>
            </div>

            <div className="fv-analytics-data">
                <Bar width={70} height={25}
                     data={this.state.data}
                     options={{
                         scales: {
                             yAxes: [{
                                 ticks: {
                                     beginAtZero: true,
                                     callback: function(value) {if (value % 1 === 0) {return value;}}
                                 }
                             }]
                         },
                         tooltips: {
                             callbacks: {
                                 title: (tooltipItem, data) =>{
                                     if(this.state.filterType == 'week')
                                         return 'week ' + data.labels[tooltipItem[0].index];
                                     else
                                         return data.labels[tooltipItem[0].index];
                                 }
                             }
                         }
                     }}
                     legend={
                         {
                             position:'bottom'
                         }
                     }
                />
            </div>

        </div>
    }
    render(){




        return [
            <TopBar/>,
                fvGlobalVar.fv_plan === 'PAID' ?
                    ((fvGlobalVar.roles['analytics'] === 'true' || fvGlobalVar.roles['analytics'] === true || fvGlobalVar.user === 'administrator') ? (
                        this.render_controls()
                    )  : <Unauthorised/> ) : this.render_controls()


        ]

    }

}


