import React, { Component } from 'react';
import {Bar} from 'react-chartjs-2';
import EntryCount from './entryCount';
export default class Dashboard extends Component{

    constructor(props) {
        super(props);
        this.state={
            from: undefined,
            to: undefined,
            formsPlugin: [],
            formName:[],
            selectedPlugin: '',
            selectedForm:'',
            data:{},
            freshLoad : true,
            wrapperRef:'',
            wrapperRefTo:'',
            options:{
                'legend':{
                    display:false,
                },
                'scales':{
                    yAxes: [{
                        ticks: {
                            min: 0,
                        }
                    }]
                },
                title: {
                    display: true,
                    text: 'Weekly Report'
                }
            }
        };
    }

    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside.bind(this));
        //this.getSavedSettingData();
        this.getData();
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside.bind(this));
    }

    setWrapperRef(node) {
        this.state.wrapperRef = node;
    }
    setwrapperRefTo(node) {
        this.state.wrapperRefTo = node;
    }

    handleClickOutside(event) {
        if (this.state.wrapperRef && !this.state.wrapperRef.contains(event.target)) {
            this.setState({isVisible : false});
        }
        if (this.state.wrapperRefTo && !this.state.wrapperRefTo.contains(event.target)) {
            this.setState({isVisibleto : false});
        }
    }

    getSavedSettingData(){
        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_dbpanel_settings/`;
        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
        }).then(response => response.json()).then(resData => {
            //console.log('res data',resData)
        });
    }

    fillForms(value) {
        const BASE_URL = fvGlobalVar.rest_url + `wts-fv/get_forms/`;

        fetch(BASE_URL, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'dataType' : 'json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                post_type: value,
            }),
        }).then(response => response.json()).then(data => {
            this.setState({formName : data}, ()=> { this.setState({selectedForm : Object.keys(data)[0]},() => {if(this.state.checkChange === true){
                this.getData();
                this.saveSetting();
                this.setState({checkChange:false});
            }})});
        });
    }

    saveSetting(){
        let settings = this.props;
        let dashboardNo = this.props.dashboard_number;

        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_dbpanel_settings/`;
        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
        }).then(response => response.json()).then(data => {
            if(data !== false){
                let setting_panel = data.panelData;
                setting_panel[dashboardNo].selectedPlugin = this.state.selectedPlugin;
                setting_panel[dashboardNo].selectedForm = this.state.selectedForm;
                setting_panel[dashboardNo].formName = this.state.formName;
                setting_panel[dashboardNo].queryType = this.state.queryType;

                // save changed settings.

                let BASE_URL = fvGlobalVar.rest_url + `wts-fv/set_dbpanel_settings/`;
                fetch(BASE_URL, {
                    method: 'POST',
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'X-WP-Nonce' : fvGlobalVar.nonce
                    },
                    body: JSON.stringify({
                        dbPanel: data.panelNumber,
                        panelData: setting_panel,
                    })
                }).then(response => response.json()).then(data => {

                });
            }
        });
    }

    getData(){
        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_analytic_data/`;
        let param = {};
        if(this.state.selectedPlugin === '' && this.state.selectedForm === ''){
            param['queryType'] = this.props.widgetsData.queryType;
            param['fromDate'] = 'undefined';
            param['toDate'] = 'undefined';
            param['pluginName'] = this.props.widgetsData.selectedPlugin;
            param['formName'] = this.props.widgetsData.selectedForm;
            param['filterType'] = 'day';
            /*BASE_URL = BASE_URL + '?queryType='+ this.props.widgetsData.queryType;
            BASE_URL = BASE_URL + '&fromDate=undefined';
            BASE_URL = BASE_URL + '&toDate=undefined';
            BASE_URL = BASE_URL + '&pluginName='+ this.props.widgetsData.selectedPlugin;
            BASE_URL = BASE_URL + '&formName='+ this.props.widgetsData.selectedForm;
            BASE_URL = BASE_URL + '&filterType=day';*/
            this.getSettingData(BASE_URL,param);
        }else{
            param['queryType'] = this.state.queryType;
            param['fromDate'] = 'undefined';
            param['toDate'] = 'undefined';
            param['pluginName'] = this.state.selectedPlugin;
            param['formName'] = this.state.selectedForm;
            param['filterType'] = 'day';
            /*BASE_URL = BASE_URL + '?queryType='+this.state.queryType;
            BASE_URL = BASE_URL + '&fromDate=undefined';
            BASE_URL = BASE_URL + '&toDate=undefined';
            BASE_URL = BASE_URL + '&pluginName='+ this.state.selectedPlugin;
            BASE_URL = BASE_URL + '&formName='+ this.state.selectedForm;
            BASE_URL = BASE_URL + '&filterType=day';*/
            this.getSettingData(BASE_URL,param);
        }
    }

    getSettingData(BASE_URL,param){
        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'dataType' : 'json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                data: param,
            })
        }).then(response => response.json()).then(resData => {
            if(resData == null){
                var plugins = {no_plugin: "No Plugin"};
                var forms = {'no_form' : { 'id' : 'no_form', 'name' : 'No Form'}};
                this.setState({formName : forms});
                this.setState({ formsPlugin: plugins });
                return;
            }
            this.setState({ formsPlugin: resData.forms_plugin });


            if(this.state.freshLoad === true){
                this.setState({freshLoad: false});

                let form_plugin = resData['forms_plugin'];

                this.setState({selectedPlugin : this.props.widgetsData.selectedPlugin});
                this.setState({selectedFormValue:this.props.widgetsData.selectedForm});
                this.setState({queryType:this.props.widgetsData.queryType});
                this.fillForms(this.props.widgetsData.selectedPlugin);
            }

            let chartData = [];
            chartData['labels'] = [];
            chartData['datasets'] = [];
            chartData['data'] = [];

            resData.data.map((row, key) =>
            {
                chartData['labels'].push(row.Label);
                chartData['data'].push(row.count);
            });
            this.setState({data :{
                    labels : chartData['labels'],
                    datasets: [{
                        label: 'Leads',
                        backgroundColor: "#0087be",
                        hoverBackgroundColor: "#00aadc",
                        data: chartData['data'],
                    }]
                } });

        });
    }
    handleClick(event){
        event.preventDefault();
        this.getData();
    }

    filterData(event){
        this.setState({filterType : event.target.name},()=>{this.getData()});
    }

    render(){
        return[
            <div className="fv-analytics-dashboard-wrapper">
                <div className="fv-control-dashboard-wrapper">
                    <form id={`fv-db-controls-${this.props.dashboard_number}`} class="fv-db-widget-form">
                        <div className="fv-forms-control-dashboard-wrapper">
                            <div>
                                <label for={`ddlPlugin-${this.props.dashboard_number}`}
                                       className="fv-form--dashboard-control">Plugin</label>
                                <select id={`ddlPlugin-${this.props.dashboard_number}`}
                                        className="fv-form--dashboard-control" value={this.state.selectedPlugin}
                                        onChange={(event) => this.setState({selectedPlugin: event.target.value}, () => {
                                            this.fillForms(this.state.selectedPlugin);
                                            this.setState({selectedFormValue: this.state.selectedForm}, () => {
                                                this.setState({checkChange: true});
                                            });
                                        })}>
                                    {
                                        Object.keys(this.state.formsPlugin).map(key =>
                                            <option value={key}>{this.state.formsPlugin[key]}</option>
                                        )
                                    }
                                </select>
                            </div>
                            <div>
                                <label for={`ddlForm-${this.props.dashboard_number}`}
                                       className="fv-form--dashboard-control">Form</label>
                                <select id={`ddlForm-${this.props.dashboard_number}`}
                                        className="fv-form--dashboard-control" value={this.state.selectedFormValue}
                                        onChange={(event) => {
                                            this.setState({selectedFormValue: event.target.value});
                                            this.setState({selectedForm: event.target.value}, () => {
                                                this.getData();
                                                this.saveSetting();
                                            })
                                        }}>
                                    {
                                        Object.keys(this.state.formName).map(key =>
                                            <option
                                                value={this.state.formName[key].id}>{this.state.formName[key].name}</option>
                                        )
                                    }
                                </select>
                            </div>
                            <div>
                                <label for={`ddlTime-${this.props.dashboard_number}`}
                                       className="fv-form--dashboard-control">Duration</label>
                                <select id={`ddlTime-${this.props.dashboard_number}`} className="fv-form-control"
                                        value={this.state.queryType}
                                        onChange={(event) => this.setState({queryType: event.target.value}, () => {
                                            this.getData();
                                            this.saveSetting();
                                        })}>
                                    <option value="Last_7_Days">Last 7 Days</option>
                                    <option value="Last_30_Days">Last 30 Days</option>
                                    <option value="This_Week">This Week</option>
                                    <option value="This_Month">This Month</option>
                                </select>
                            </div>
                        </div>
                    </form>
                </div>

                <div className="fv-dashboard-analytics-data">
                    <Bar width={100} height={60}
                         data={this.state.data}
                         options={{
                             scales: {
                                 yAxes: [{
                                     ticks: {
                                         beginAtZero: true,
                                         callback: function(value) {if (value % 1 === 0) {return value;}}
                                     }
                                 }]
                             },
                             tooltips: {
                                 callbacks: {
                                     title: (tooltipItem, data) =>{
                                         if(this.state.filterType == 'week')
                                             return 'week ' + data.labels[tooltipItem[0].index];
                                         else
                                             return data.labels[tooltipItem[0].index];
                                     }
                                 }
                             }
                         }}
                         legend={
                             {
                                 position:'bottom'
                             }
                         }
                    />
                </div>
                <hr/>
                <EntryCount duration={this.state.queryType}/>
            </div>
        ]
    }
}
