import React, {Component} from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const grid = 5;

const getItemStyle = (isDragging, draggableStyle) => ({
    // some basic styles to make the items look a bit nicer
    userSelect: 'none',
    padding: grid * 2,
    margin: `0 auto`,

    // change background colour if dragging
    background: isDragging ? '#1993b8' : '#859E91',

    // styles we need to apply on draggables
    ...draggableStyle,
});

const getListStyle = isDraggingOver => ({
    background: isDraggingOver ? '#4fe9ef' : '#50b3f9',
    padding: 0,
});

class ColumnAliasItem extends Component{
    constructor(props) {
        super(props);
        this.state = {
            items: this.props.col
        };
    }

    render() {
        return [
            <DragDropContext onDragEnd={this.props.onDragEnd}>
                <Droppable droppableId="droppable">
                    {(provided, snapshot) => (
                        <div
                            ref={provided.innerRef}
                            style={getListStyle(snapshot.isDraggingOver)}
                            className={'popup-li-content'}
                        >
                            {
                                Object.values(this.props.col).map((item, index) => (
                               // console.log('item -> ', item, 'index -> ', index),
                                <Draggable key={item.colKey} draggableId={item.colKey} index={index} tabindex="-1">
                            {(provided, snapshot) => (
                                <div
                                ref={provided.innerRef}
                                {...provided.draggableProps}
                                {...provided.dragHandleProps}
                                style={getItemStyle(
                                snapshot.isDragging,
                                provided.draggableProps.style,
                            )}
                                tabIndex="-1"
                                >
                                    <li className='popup-li'>
                                        <span className={'label'} tabindex="-1">{item.colKey}</span>
                                        <input className={'inputbox-style'} type="text" defaultValue={item.alias} name="alias" onChange={ event => this.props.updateColumnAlias(event,index)} />
                                        <input className={'checkbox-style'} tabindex="-1" type="checkbox" defaultChecked={item.visible} name="enabled" onChange={event => this.props.updateColumnAlias(event,index)} />
                                    </li>
                                </div>
                                )}
                                </Draggable>
                            ))}
                            {provided.placeholder}
                        </div>
                    )}
                </Droppable>
            </DragDropContext>
        ];
    }

}

export default ColumnAliasItem;