import React, { Component } from 'react';
const { Button,Dropdown,RadioControl } =  wp.components;
const { __ } = wp.i18n;
const { Fragment } = wp.element;
export default class Configuration extends Component {
    constructor(props) {
        super(props);
        this.state = {
            formsPlugin:[],
            dbPanel : 0,
            panelState : [],
            saveIP : 'no'
        };
        this.get_plugins();
        this.get_db_data();
    }

    get_db_data(){
        let Get_Ip_Setting = fvGlobalVar.rest_url + `wts-fv/get_ip_settings/`;
        fetch(Get_Ip_Setting,{
            method:'POST',
            headers: {
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
        }).then(response => response.json()).then(data => {
            if(data !== false){
                this.setState({ saveIP : data });
            }
        });

        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_dbpanel_settings/`;
        fetch(BASE_URL,{
            method:'POST',
            headers: {
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
        }).then(response => response.json()).then(data => {
            if(data !== false){
                this.setState({panelState: data.panelData});
                this.setState({dbPanel: data.panelNumber});
            }

            /*let panelState = this.state.panelState;
            panelState[0] = { "selectedPlugin" : Object.keys(data)[0] };

            this.setState({panelState : panelState, formsPlugin : data}
                ,() => {this.fillForms(Object.keys(data)[0],"0")}
            );*/
        });
    }
    fillForms(value,index) {
        const BASE_URL = fvGlobalVar.rest_url + `wts-fv/get_forms/`;

        fetch(BASE_URL, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'dataType' : 'json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                post_type: value,
            }),
        }).then(response => response.json()).then(data => {
            let panelState = this.state.panelState;
            if(panelState == undefined){
                panelState = [];
                panelState[index] = {};
            }
            panelState[index].formName = data ;
            panelState[index].selectedForm = Object.keys(data)[0];
            this.setState({panelState: panelState});
        });
    }

    get_plugins(){
        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/get_plugins/`;
        fetch(BASE_URL,{
            method:'GET',
            headers: {
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
        }).then(response => response.json()).then(data => {
            this.setState({formsPlugin : data});
        });
    }

    updateDbPanel(value){
        let panelState = this.state.panelState;
        this.setState({dbPanel : value},() => {
            for (let i = 0; i < this.state.dbPanel; i++) {
                if(panelState[i] === undefined){
                    panelState[i] = {};

                    panelState[i].queryType = "Last_30_Days";
                    panelState[i].selectedPlugin = Object.keys(this.state.formsPlugin)[0];
                    this.fillForms(Object.keys(this.state.formsPlugin)[0],i)
                }
            }
            this.setState({panelState : panelState});
        });

    }

    saveSettings(event){
        this.props.set_status('true');
        jQuery.ajax({
            type: 'POST',
            url: fvGlobalVar.ajax_url,
            data: {
                action : 'fv_save_config',
                dbPanel: this.state.dbPanel,
                panelData: this.state.panelState,
                saveIP: this.state.saveIP,
            },
        })
            .done(function(data) {
                this.props.set_status('false');
            }.bind(this))
            .fail(function(jqXhr) {
                console.log('failed to save settings');
            });
    }

    renderOptions(l){
        let options = [];
        if(fvGlobalVar.fv_plan == 'FREE'){
            options[0] = <option value='1'>Enable</option>;
            options[1] = <option value='0'>Disable</option>;
        }
        else{
            for(let i=0; i<= l; i++){
                options[i] = <option value={`${i}`}>{`${i}`}</option>;
            }
        }

        return options;
    }

    render() {
        return [
            <form method="POST">
                <div class="configuration-wrapper">
                    <div className={'fv-control-wrapper'}>
                        <span className='labels'>Dashboard Widgets</span>
                        <select name="dbWidgetNum" id='ddlPlugin' value={this.state.dbPanel} className="fv-form-control"
                                onChange={(event) => this.updateDbPanel(event.target.value)}>

                            { this.renderOptions(fvGlobalVar.widget_num)}
                        </select>
                    </div>
                    <div className={'fv-control-wrapper'}>
                        <span className='labels'>Save IP Address</span>
                        <RadioControl
                            selected={ this.state.saveIP }
                            className='fv-rb-option'
                            options={ [
                                { value: 'yes', label: 'Yes' },
                                { value: 'no', label: 'No' },
                            ] }
                            onChange={ ( option ) => { this.setState( { saveIP : option } ) } }
                        />
                    </div>
                    <div className={'fv-control-wrapper'}>
                        <Button name="btnSaveDbSettings" isPrimary onClick={this.saveSettings.bind(this)}>
                            Save Data
                        </Button>
                    </div>
                </div>
            </form>
        ]
    }
}