import React, { Component } from 'react';
import Popup from "reactjs-popup";

export default class DeleteTableRow extends Component {
    constructor(props){
        super(props);

        this.state = {
            visible  : false,
            disabled: false
        };
        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.openModal();
        //console.log('cons', this.props);
    }


    openModal() {
        console.log('open model', this.props);
        this.setState({
            visible : true
        },() => {
            if(this.props.selectedPlugin == 'caldera' || this.props.selectedPlugin === 'ninja'){
                this.setState({disabled:true});
            }else{
                this.setState({disabled:false});
            }
        });
    }

    closeModal() {
        console.log('close model');
        this.setState({
            visible : false
        });
        this.props.setDeleteClick(false);
    }

    componentWillReceiveProps(nextProps, nextContext) {
        nextProps.checkDeleteClick > 0 ? this.openModal():'';
    }

    componentDidMount() {
        this.openModal();
    }

    saveChanges() {
        let BASE_URL= fvGlobalVar.rest_url + `wts-fv/delete_row_id/` ;

        fetch(BASE_URL, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'X-WP-Nonce' : fvGlobalVar.nonce
            },
            body: JSON.stringify({
                id: this.props.deleteRowId
            })
        }).then(response => response.json()).then(data => {
            this.props.runFetch();
        });
        this.closeModal();
    }
    render() {
        return[
            <div className={'fv-delete-wrapper'}>
                <h2>Are You Sure You <br /> Want To Delete { this.props.deleteRowCount } { this.props.deleteRowCount >1 ? 'Records?':'Record?' } </h2>
                <hr/>
                {/*<button onClick={event => {this.saveChanges()}} className={'button button-link-delete'}>DELETE!</button>*/}
                <button className="push_button red" disabled={this.state.disabled} onClick={event => {this.props.deleteRowCount > 0 ? this.saveChanges() : ''}}>DELETE!</button>
                {/*<a className="push_button red" href="#"  onClick={event => {this.props.deleteRowCount > 0 ? this.saveChanges() : ''}}>DELETE!</a>*/}
            </div>
        ]
    }
}
