import React, { Component } from 'react';
import Popup from "reactjs-popup";

export default class ExportToCsv extends Component {
    constructor(props){
        super(props);

        this.state = {
            visible  : false,
            pro: true,
            disabled:false
        };
    }

    setRowsChoice(event){
        console.log('in exprot -> ', event.target.checked);
        this.props.setColumnsIdsChoice(event.target.checked);
    }

    componentWillReceiveProps(nextProps, nextContext) {

    }

    render() {
        if(this.state.pro){
            return[
                    <div className={'fv-export-wrapper export-options'}>

                        <label className="radio" htmlFor="all">
                            <input
                                id="all"
                                value="all"
                                name="export"
                                type="radio"
                                defaultChecked
                                onChange={event => this.props.setColumnsChoice(event.target.value)}
                            />
                            <span>Export all Columns</span>
                        </label>
                        <label className="radio" htmlFor="custom">
                            <input
                                id="custom"
                                value="custom"
                                name="export"
                                type="radio"
                                onChange={event => this.props.setColumnsChoice(event.target.value)}
                            />
                            <span>Export Visible Columns Only</span>
                        </label>
                        <label className="checkboxes">
                            <input
                                id="custom-rows"
                                value="custom-rows"
                                name="export"
                                type="checkbox"
                                className="checkbox cb-expo-selected"
                                disabled={!this.props.disableCheckBox}
                                onChange={event => this.setRowsChoice(event)}
                            />
                            <span>Export Selected Rows Only</span>
                        </label>
                            <div className={'popup-footer-export'}>
                                <button className={'button fv-btn-save-setting'} name="btnExport">Export to CSV</button>
                                {/*<a className="push_button blue" href="#" name="btnExport" onClick={event => {event.preventDefault()}}>Export to CSV</a>*/}
                            </div>
                    </div>
            ]
        }
    }
}
